import React, { useRef } from 'react';

import { Canvas } from 'react-three-fiber';
import { OrbitControls } from '@react-three/drei';
import { useGLTF } from '@react-three/drei';

function Model({ objectPath, ...props }) {
    const { scene } = useGLTF(objectPath);
    return <primitive object={scene.clone(true)} {...props} />;
}

function ObjectsCanvas(props) {
    const controls = useRef();

    return (
        <div className='d-flex bg-dark align-items-center flex-column' style={{ width: '100%', height: '100vh' }}>
            <Canvas
                camera={{ position: [65, 65, 65] }}
                onCreated={({ gl, camera }) => {
                    gl.shadowMap.enabled = true;
                }}
            >
                {/* Ambient light intensity */}
                <ambientLight intensity={5} />

                {/* Directional light */}
                <directionalLight intensity={22} position={[25, 25, 33]} />

                {/* Model */}
                <Model objectPath={`/3dobjects/${props.objectsThreejs}.glb`} scale={[6, 6, 6]} position={[10, -26, 10]} />

                {/* Controls */}
                <OrbitControls ref={controls} />

                {/* AxesHelper (optional, for visualization) */}
                <axesHelper args={[50]} />
            </Canvas>
        </div>
    );
}

export default ObjectsCanvas;
